@charset "utf-8";

/* =========================================================
header
========================================================= */
@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}
.header {
  animation: fadeIn 0.8s linear forwards;
  &__title {
    text-align: center;
    img {
      vertical-align: bottom;
    }
  }
}
@media screen and (min-width:769px) {
  .header {
    &__title {
      img {
        width: auto;
        max-width: 100%;
      }
    }
  }
}


/* =========================================================
Main
========================================================= */
@media screen and (min-width: 769px) {
  .main-contents {
    padding: 0 20px;
  }
}


/* =========================================================
Local navi
========================================================= */
.localnav {
  max-width: 1200px;
  margin: 80px auto 0;
  padding: 0 20px;
  @media screen and (min-width: 769px) {
    padding: 0;
  }
  @media screen and (max-width: 768px) {
    margin-top: 30px;
  }
  &-list {
    display: flex;
    flex-wrap: wrap;
  }
  &-item {
    width: calc(50% - 10px);
    margin: 20px 20px 0 0;
    background: #C2A036;
    color: #fff;
    transition: 0.4s;
    @media screen and (max-width: 768px) {
      width: calc(50% - 5px);
      margin: 10px 10px 0 0;
    }
    &:hover{
      background: #E2927C;
    }
    &:nth-child(-n+2) {
      margin-top: 0;
    }
    &:nth-child(2n) {
      margin-right: 0;
    }
    a,
    button {
      width: 100%;
      height: 60px;
      font-size: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #fff;
      text-decoration: none;
      font-family: 'Montserrat', sans-serif;
    }
  }
}


/* =========================================================
New&Renew items
========================================================= */
[v-cloak] {
  display: none;
}

.newrenew-block {
  max-width: 1200px;
  margin: 50px auto 0;
  padding: 0 20px;
  @media screen and (min-width: 769px) {
    padding: 0;
  }
  @media screen and (max-width: 768px) {
    margin: 20px auto 0;
  }
  .read-ttl{
    text-align: center;
    font-size: 40px;
    line-height: 50px;
    margin-bottom: 40px;
    font-family: 'Montserrat', sans-serif;
    position: relative;
    color: #C8523D;
    z-index: 10;
    @media screen and (max-width: 768px) {
      font-size: 30px;
      line-height: 40px;
      margin-bottom: 30px;
    }
    &::after{
      z-index: -1;
      content: " ";
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      margin: auto;
      width: 100%;
      height: 2px;
      background: #1A1311;
    }
    span{
      display: inline-block;
      padding: 0 20px;
      background: #CED5CC;
      z-index: 2;
    }
  }
  .list-block {
    margin: 0 auto 50px;
    display: none;
    &#all-list-block{
      display: block;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  .ttl {
    margin: 0 auto 20px;
    font-size: 18px;
    font-weight: bold;
    text-align: center;
  }
}

.item-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  @media screen and (min-width: 769px) {
    width: calc(100% + 20px);
    margin-left: -10px;
    justify-content: initial;
  }
  .item {
    width: 48%;
    margin: 0 0 20px;
    box-sizing: border-box;
    @media screen and (min-width: 769px) {
      width: 33.33%;
      padding: 0 10px;
    }
  }
  .front-cont {
    height: 100%;
    background: #fff;
    @media screen and (max-width: 768px) {
      padding: 0 0 30px 0;
      box-sizing: border-box;
      position: relative;
    }
    .img-wrap {
      position: relative;
    }
    .badge-l {
      width: 80px;
      padding: 10px;
      background: #C8523D;
      position: absolute;
      color: #fff;
      font-family: 'Montserrat', sans-serif;
      top: -10px;
      left: -10px;
      box-sizing: border-box;
      @media screen and (max-width: 768px) {
        text-align: left;
        width: 90px;
        height: 60px;
        padding: 5px 0 0 10px;
      }
      &::after{
        content: " ";
        position: absolute;
        top: 100%;
        left: 0;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 25px 80px 0 0;
        border-color: #C8523D transparent transparent transparent;
        @media screen and (max-width: 768px) {
          border-width: 60px 15px 0 0;
          top: 0;
          left: 100%;
        }
      }
      .date {
        font-size: 24px;
        line-height: 27px;
        text-align: center;
        @media screen and (max-width: 768px) {
          font-size: 16px;
        }
        br{
          @media screen and (max-width: 768px) {
            display: none;
          }
        }
        b{
          display: block;
          font-weight: 400;
          font-size: 16px;
          @media screen and (max-width: 768px) {
            display: inline-block;
            font-size: 16px;
            padding-left: 5px;
          }
        }
      }
      .new-renew {
        margin: 5px 0 0;
        padding: 10px 0 0;
        border-top: 1px solid #fff;
        font-size: 15px;
        text-align: center;
        @media screen and (max-width: 768px) {
          font-size: 16px;
          margin: 0 0 0;
          padding: 5px 0 0;
        }
      }
    }
    .badge-r {
      width: 80px;
      height: 80px;
      background: #fff;
      border: 1px solid #000;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 15px;
      position: absolute;
      top: 0;
      right: 0;
    }
    .detail {
      padding: 20px 0 0;
      @media screen and (max-width: 768px) {
        padding: 50px 0 0;
      }
    }
    .logo {
      width: 70%;
      max-width: 250px;
      margin: 0 auto 20px;
      @media screen and (max-width: 768px) {
        margin: 0 auto 10px;
      }
    }
    .miniwrap{
      padding: 20px;
    }
    .shop {
      margin: 0 auto 0;
      font-size: 16px;
      text-align: center;
    }
    .floor,
    .ctg {
      margin: 15px auto 0;
      font-size: 14px;
      text-align: center;
    }
    .more {
      width: 100%;
      margin: 20px auto 0;
      @media screen and (max-width: 768px) {
        position: absolute;
        bottom: 0;
        left: 0;
      }
      button {
        width: 100%;
        height: 50px;
        background: #C2A036;
        color: #fff;
        font-size: 20px;
        transition: 0.4s;
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: 'Montserrat', sans-serif;
        @media screen and (max-width: 768px) {
          font-size: 16px;
          height: 35px;
        }
        &:hover{
          background: #E2927C;
        }
      }
    }
  }
}

.newrenew-modal {
  .modal-wrap {
    width: calc(100vw - 80px);
    max-width: 1024px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    
    @media screen and (max-width: 768px) {
      width: calc(100vw - 40px);
      top: inherit;
      left: inherit;
      margin: 0 auto;
      position: relative;
      transform: inherit;
      overflow-y: initial;
      background: #fff;
      padding-top: 40px;
    }
  }
  .modal-cont {
    background: #fff;
    box-sizing: border-box;
    @media screen and (min-width: 769px) {
      max-height: calc(100vh - 150px);
      display: -webkit-box;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      width: 100%;
      align-items: center;
      padding-top: 0;
      box-sizing: initial;
      overflow-y: scroll;
    }
  }
  .img-wrap {
    position: relative;
    @media screen and (min-width: 769px) {
      width: 50%;
      box-sizing: border-box;
      padding: 70px 20px 20px;
    }
  }
  .img-slider {
    position: relative;
    .slick-arrow {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      z-index: 1;
      transition: 0.4s;
      &:hover{
        opacity: 0.6;
      }
      &.prev {
        left: 10px;
        @media screen and (max-width: 768px) {
          left: 5px;
        }
      }
      &.next {
        right: 10px;
        @media screen and (max-width: 768px) {
          right: 5px;
        }
      }
      button {
        display: block;
        width: 35px;
        height: 35px;
        background: #fff;
        border: 1px solid #C8523D;
        border-radius: 100%;
        &:before {
          content: "";
          display: block;
          width: 9px;
          height: 9px;
          border-top: 1px solid #C8523D;
          border-left: 1px solid #C8523D;
          position: absolute;
          top: 12px;
          left: 15px;
          transform: rotate(-45deg);
        }
        &.slick-next {
          transform: scale(-1,-1);
        }
      }
    }
  }
  .detail {
    padding: 20px;
    box-sizing: border-box;
    @media screen and (min-width: 769px) {
      width: 50%;
    }
  }
  .catchfirst{
      max-width: 110px;
      position: absolute;
      top: -20px;
      right: -20px;
      z-index: 10;
      text-align: center;
      @media screen and (max-width: 768px) {
        max-width: 80px;
        right: -10px;
      }
      span{
        color: #fff;
        font-weight: bold;
        font-size: 15px;
        line-height: 22px;
        display: inline-block;
        position: absolute;
        top: 47%;
        left: 50%;
        transform: translateY(-50%) translateX(-50%);
        width: 95%;
        white-space: pre-line;
        @media screen and (max-width: 768px) {
          font-size: 13px;
          line-height: 18px;
          top: 45%;
        }
      }
  }
  .side-wrap{
    background: #C8523D;
    text-align: left;
    position: absolute;
    top: -10px;
    left: -10px;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
    box-sizing: border-box;
    padding: 13px 20px 0;
    height: 65px;
    z-index: 100;
    @media screen and (max-width: 768px) {
      height: 55px;
      padding: 10px 10px 0;
      top: -25px;
    }
    &::after{
      content: " ";
      position: absolute;
      top: 0;
      left: 100%;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 65px 20px 0 0;
      border-color: #C8523D transparent transparent transparent;
      @media screen and (max-width: 768px) {
        border-width: 55px 20px 0 0;
      }
    }
  }
  .date {
    font-size: 34px;
    display: inline-block;
    vertical-align: middle;
    @media screen and (max-width: 768px) {
      font-size: 24px;
      line-height: 35px;
    }
    b{
      font-size: 22px;
      padding-left: 5px;
      display: inline-block;
      @media screen and (max-width: 768px) {
        font-size: 20px;
      }
    }
  }
  .new-renew {
    display: inline-block;
    vertical-align: top;
    font-size: 24px;
    border-left: 1px solid #fff;
    padding-left: 15px;
    line-height: 40px;
    margin-left: 10px;
    @media screen and (max-width: 768px) {
      font-size: 20px;
      line-height: 35px;
    }
  }
  .first {
    margin: .8em auto 0;
    text-align: center;
    font-size: 15px;
  }
  .logo {
    width: 70%;
    max-width: 205px;
    margin: 15px auto 0;
    @media screen and (max-width: 768px) {
      margin: 0 auto;
    }
  }
  .miniwrap{
    border-top: 1px solid #DCDCDC;
    border-bottom: 1px solid #DCDCDC;
    box-sizing: border-box;
    padding: 20px;
    margin-top: 20px;
  }
  .shop {
    font-size: 16px;
    text-align: center;
    font-weight: 500;
  }
  .floor,
  .ctg {
    margin: 10px auto 0;
    font-size: 14px;
    text-align: center;
  }
  .introduction {
    margin: 20px auto 0;
    font-size: 14px;
    line-height: 25px;
  }
  .open-event {
    margin: 20px auto 0;
    .ttl {
      font-size: 16px;
      font-weight: bold;
      text-align: left;
      margin-bottom: 0;
      color: #C8523D;
    }
    .txt {
      margin: .5em auto 0;
      font-size: 14px;
      line-height: 25px;
    }
  }
  .link-list {
    width: 100%;
    margin: 10px auto 0;
    padding-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .link-item {
    width: 40px;
    margin: 0 10px 0 0;
    &:last-child {
      margin-right: 0;
    }
    a {
      display: block;
      background: #C2A036;
      border-radius: 100%;
      transition: 0.4s;
      &:hover{
        background: #C8523D;
      }
    }
  }
  .modal-arrow {
    position: absolute;
    width: 50px;
    height: 50px;
    transition: 0.4s;
    &:hover{
      opacity: 0.6;
    }
    @media screen and (max-width: 768px) {
      width: 35px;
      height: 35px;
    }
    button {
      display: block;
      background: #fff;
      border-radius: 100%;
      width: 50px;
      height: 50px;
      background: #C2A036;
      @media screen and (max-width: 768px) {
        width: 35px;
        height: 35px;
      }
      &::after{
        content: " ";
        position: absolute;
        width: 12px;
        height: 12px;
        border-right: 1px solid #fff;
        border-bottom: 1px solid #fff;
        transform: rotate(-45deg);
        top: 0;
        left: -7px;
        right: 0;
        bottom: 0;
        margin: auto;
        @media screen and (max-width: 768px) {
          width: 7px;
          height: 7px;
        }
      }
    }
    &.prev {
      position: absolute;
      top: 50%;
      left: calc(50% - 550px);
      transform: translateY(-50%) translateX(-50%);
      button::after {
        transform: rotate(135deg);
        left: 0px;
        right: -7px;
      }
      @media screen and (max-width: 1180px) {
        left: 10px;
        transform: translateY(-50%);
      }
      @media screen and (max-width: 768px) {
        transform: initial!important;
        top: initial!important;
        bottom: -10px;
        left: 20px;
        position:relative;
      }
    }
    &.next {
      position: absolute;
      top: 50%;
      left: calc(50% + 550px);
      transform: translateY(-50%) translateX(-50%);
      @media screen and (max-width: 1180px) {
        right: 10px;
        left: initial;
        transform: translateY(-50%);
      }
      @media screen and (max-width: 768px) {
        transform: initial!important;
        top: initial!important;
        bottom: 25px;
        right: 20px;
        position:relative;
        margin-left: auto;
      }
    }
  }
}
.item-list li:last-child .modal-arrow-close{
  margin-top: -18px;
}

@media screen and (max-width: 768px) {
.modal-arrow-close {
   text-align: center;
