  .logo {
    width: 70%;
    max-width: 205px;
    margin: 15px auto 0;
  }
  .miniwrap{
    border-top: 1px solid #DCDCDC;
    box-sizing: border-box;
    padding: 20px 20px 0;
    max-width: 500px;
    margin: 20px auto 0;
  }
  .shop {
    font-size: 16px;
    text-align: center;
    font-weight: 500;
  }
  .floor,
  .ctg {
    margin: 20px auto 0;
    font-size: 14px;
    text-align: center;
  }
  .introduction {
    margin: 20px auto 0;
    font-size: 14px;
    line-height: 25px;
    text-align: center;
    max-width: 500px;
    margin: 20px auto;
  }
  .open-event {
    margin: 0 auto 40px;
    text-align: center;
    .ttl {
      font-size: 16px;
      font-weight: bold;
      margin-bottom: 0;
      color: #C8523D;
    }
    .txt {
      margin: .5em auto 0;
      font-size: 14px;
      line-height: 25px;
    }
  }
  .link-list {
    width: 100%;
    margin: 10px auto 0;
    padding-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .link-item {
    width: 40px;
    margin: 0 10px 0 0;
    &:last-child {
      margin-right: 0;
    }
    a {
      display: block;
      background: #C2A036;
      border-radius: 100%;
      transition: 0.4s;
      &:hover{
        background: #C8523D;
      }
    }
  }
  .modal-arrow {
    position: absolute;
    width: 50px;
    height: 50px;
    @media screen and (max-width: 768px) {
      width: 35px;
      height: 35px;
    }
    button {
      display: block;
      background: #fff;
      border-radius: 100%;
      width: 50px;
      height: 50px;
      background: #C2A036;
      @media screen and (max-width: 768px) {
        width: 35px;
        height: 35px;
      }
      &::after{
        content: " ";
        position: absolute;
        width: 12px;
        height: 12px;
        border-right: 1px solid #fff;
        border-bottom: 1px solid #fff;
        transform: rotate(-45deg);
        top: 0;
        left: -7px;
        right: 0;
        bottom: 0;
        margin: auto;
        @media screen and (max-width: 768px) {
          width: 7px;
          height: 7px;
        }
      }
    }
    &.prev {
      position: absolute;
      top: 50%;
      left: calc(50% - 550px);
      transform: translateY(-50%) translateX(-50%);
      button::after {
        transform: rotate(135deg);
        left: 0px;
        right: -7px;
      }
      @media screen and (max-width: 1180px) {
        left: 10px;
        transform: translateY(-50%);
      }
      @media screen and (max-width: 768px) {
        transform: initial!important;
        top: initial!important;
        bottom: 30px;
        left: 20px;
      }
    }
    &.next {
      position: absolute;
      top: 50%;
      left: calc(50% + 550px);
      transform: translateY(-50%) translateX(-50%);
      @media screen and (max-width: 1180px) {
        right: 10px;
        left: initial;
        transform: translateY(-50%);
      }
      @media screen and (max-width: 768px) {
        transform: initial!important;
        top: initial!important;
        bottom: 30px;
        right: 20px;
      }
    }
  }

}

.item-list-all .item:first-child .modal-arrow.prev button{
  display: none!important;
}
#ladies-list-block .item-list-pickup.js-tabarea{
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  & > li{width: 100%;}
  & > li:nth-child(1){order: 1;}
  & > li:nth-child(2){order: 3;}
  & > li:nth-child(3){order: 2;}
}